.nav-mobile {
  text-transform: uppercase;
  position: absolute;
  z-index: 999;
  width: 100vw;
  display: flex;
  justify-content: flex-end;
  padding: 2rem 2rem;
}
@media screen and (min-width: 64rem) {
  .nav-mobile {
    display: none;
  }
}
.nav-mobile .ham {
  cursor: pointer;
}
.nav-mobile .nav-ham {
  z-index: 999;
  color: var(--warna-biru);
  font-size: 8vw;
  top: 0;
  right: -100%;
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: var(--warna-cream);
}
.nav-mobile .nav-ham .link-wrapper {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.nav-mobile .nav-ham .link-wrapper .home {
  margin-bottom: 2rem;
  cursor: pointer;
  overflow: hidden;
}
.nav-mobile .nav-ham .link-wrapper .about {
  cursor: pointer;
  margin-bottom: 2rem;
  overflow: hidden;
}
.nav-mobile .nav-ham .link-wrapper .contact {
  cursor: pointer;
  overflow: hidden;
}
.nav-mobile .nav-ham .close {
  cursor: pointer;
  padding: 2rem 2rem;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  position: absolute;
}